import React from "react";



const Weather = (props) => {
    return (
        <div className="container text-white">
            <div className="cards pt-4">
                <h1>
                    {props.city}
                </h1>
                <h5 className="py-4">
                    <i className={`wi ${props.weatherIcon} display-1`} />
                </h5>

                {props.temp_fahrenheit ? (<h1 className="py-2">{props.temp_celsius}&deg;</h1>) : null}

                {/** show max and min temperatures */}
                {minmaxTemp(props.temp_min, props.temp_max)}

                {props.humidity ? (<h4 className="py-3">humidity {props.humidity} %</h4>) : null}
                <h4 className="py-3">{props.description}</h4>

            </div>
        </div>
    );
};
function minmaxTemp(min, max) {

    if (min && max) {
        return (
            <h3>
                <p>min<span className="px-4">{min}&deg; F</span></p>
                <p>max<span className="px-4">{max}&deg; F</span></p>
            </h3>
        );
    }

}

export default Weather;